










import Vue from 'vue';
import NavigationItem from '@/types/NavigationItem';

export default Vue.extend({
  name: 'NavigationItem',

  props: {
    navItem: {
      type: Object,
      default: () => new NavigationItem(),
      required: true,
    },
  },

  computed: {
    hasIcon() {
      return !!this.navItem.icon;
    },

    getRoute() {
      return this.navItem.route ? this.navItem.route : null;
    },
  },
});
