
























































import Vue from 'vue';
import { mapMutations, mapState } from 'vuex';

export default Vue.extend({
  name: 'UserMenu',

  data: () => ({
    menu: false,
  }),

  computed: {
    ...mapState('AppModule', [
      'isDarkMode',
    ]),
  },

  methods: {
    ...mapMutations('AppModule', ['toggleDarkMode']),
  },
});
