













import Vue from 'vue';
import NavigationItem from './NavigationItem.vue';

export default Vue.extend({
  name: 'NavigationGroup',

  components: {
    NavigationItem,
  },

  props: {
    navItem: {
      type: Object,
      default: () => new NavigationItem(),
      required: true,
    },
  },

  computed: {
    buildNavItem() {
      const navItem = {
        ...this.navItem,
      };
      navItem.children = [];
      return navItem;
    },
  },
});
