

































import Vue from 'vue';
import { mapMutations, mapState } from 'vuex';
import ChatMenu from './appbar/ChatMenu.vue';
import UserMenu from './appbar/UserMenu.vue';

export default Vue.extend({
  name: 'MainAppbar',

  components: {
    ChatMenu,
    UserMenu,
  },

  computed: {
    ...mapState('AppModule', [
      'navDrawerOpen',
      'isDarkMode',
    ]),
  },

  methods: {
    ...mapMutations('AppModule', ['toggleNavrawer']),
  },
});
